import React, { useState, useEffect } from 'react';

import {
  useTheme,
  useMediaQuery,
  Container,
  Box,
  Grid,
  Tab,
  Typography,
  Stack,
  FormControlLabel,
  RadioGroup,
  Radio,
  TextField,
  InputAdornment,
  Button,
  Collapse,
  Alert,
  AlertColor
} from '@mui/material';
import { TabContext, TabList, TabPanel } from '@mui/lab';
import { ethers } from 'ethers';
import { useWeb3React } from '@web3-react/core';
import { useContract } from '../../hooks/usContract';
import Layout from '../../components/Layout';
import erc20ABI from '../../contracts/abis/ERC20.json';
import lotteryABI from '../../contracts/abis/HashLottery.json';

const Bet = () => {
  const theme = useTheme();

  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });

  const [alert, setAlert] = useState(false);
  const [alertSeverity, setAlertSeverity] = useState<AlertColor>('warning');
  const [alertMessage, setAlertMessage] = useState('');

  const openAlert = (severity: AlertColor, message: string) => {
    setAlert(true);
    setAlertSeverity(severity);
    setAlertMessage(message);
  };

  const [buttonText, setButtonText] = useState('');

  const [tabValue, setTabValue] = React.useState('0');
  const handleTabChange = (event: React.SyntheticEvent, value: string) => {
    setTabValue(value);
  };

  const [number, setNumber] = useState('1');
  const handleNumberChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setNumber((event.target as HTMLInputElement).value);
  };

  const [amount, setAmount] = useState('100');

  const { account } = useWeb3React();

  const usdtContract = useContract(
    '0x58D1EAc17bb4F3Ff0D3d5FDd93A31cD372D17681',
    erc20ABI
  );

  const lotteryContract = useContract(
    '0x87D9f0d89cF97Cb6AcC0d3798f70f3259DD751E8',
    lotteryABI
  );

  const minBetAmount = ethers.utils.parseEther('100');
  const purchaseAction = async () => {
    if (lotteryContract == null || usdtContract == null) {
      openAlert('error', '');
      return;
    }
    const weiAmount = ethers.utils.parseEther(amount);
    if (weiAmount < minBetAmount) {
      openAlert('error', '');
      return;
    }
    const balance = await usdtContract!.balanceOf(account);
    if (balance < minBetAmount) {
      openAlert('error', '');
      return;
    }
    const allowance = await usdtContract!.allowance(account, lotteryContract!.address);
    if (allowance < weiAmount) {
      const approveTx = await usdtContract!.approve(lotteryContract!.address, weiAmount);
      setButtonText('...');
      await approveTx.wait();
    }
    const purchaseTx = await lotteryContract!.purchase(number, weiAmount);
    setButtonText('...');
    await purchaseTx.wait();

    setButtonText('');
    openAlert('success', '');
  };

  const claimAction = async () => {
    if (lotteryContract == null || usdtContract == null) {
      openAlert('error', '');
      return;
    }
    const claimTx = await lotteryContract!.claim();
    openAlert('info', '...');
    await claimTx.wait();

    openAlert('success', '');
  };

  const [hasEarned, setHasEarned] = useState(false);

  useEffect(() => {
    const timer = setTimeout(async () => {
      const earned = await lotteryContract!.earned(account);
      if (earned > 0) {
        setHasEarned(true);
      }
    }, 1000);
    return () => clearTimeout(timer);
  }, []);

  return (
    <Layout colorInvert={true}>
      <Container
        sx={{
          paddingY: 2.5,
        }}
      >
        <Grid
          container
          spacing={4}
          direction={isMd ? 'row' : 'column-reverse'}
          sx={{
            paddingY: 2
          }}
        >
          <Grid
            item
            alignItems={'center'}
            xs={12}
            md={7}
          >
            <Box>
              <Typography
                variant={'h4'}
                gutterBottom
                sx={{ marginBottom: 4, fontWeight: 700 }}
              >
                {''}
              </Typography>
              <Typography component={'p'} sx={{ marginBottom: 2 }}>
                {' Hash  Hash  UInt256  10 '}
              </Typography>
              <Typography component={'p'} sx={{ marginBottom: 2 }}>
                {'-  100-10000 USDT'}
              </Typography>
              <Typography component={'p'} sx={{ marginBottom: 2 }}>
                {'-  2 '}
              </Typography>
              <Typography component={'p'} sx={{ marginBottom: 2, paddingLeft: 2 }}>
                {'1/2/3/4'}
                {'6/7/8/9'}
                {'1/3/7/9'}
                {'2/4/6/8'}
              </Typography>
              <Typography component={'p'} sx={{ marginBottom: 2 }}>
                {'-  4 '}
              </Typography>
              <Typography component={'p'} sx={{ marginBottom: 2, paddingLeft: 2 }}>
                {'2/4'}
                {'1/3'}
                {'6/8'}
                {'7/9'}
              </Typography>
              <Typography component={'p'} sx={{ marginBottom: 2 }}>
                {'-  8 '}
              </Typography>
              <Typography component={'p'} sx={{ marginBottom: 2, paddingLeft: 2 }}>
                {'0'}
                {'5'}
              </Typography>
            </Box>
          </Grid>
          <Grid
            item
            alignItems={'center'}
            xs={12}
            md={5}
          >
            <Box
              sx={{
                minHeight: '100%',
                padding: 2,
                borderRadius: 4,
                backgroundColor: 'alternate.main'
              }}
            >
              <TabContext value={tabValue}>
                <TabList onChange={handleTabChange} sx={{ borderBottom: 1, borderColor: 'divider' }}>
                  <Tab label="" value="0" />
                  <Tab label="" value="1" />
                </TabList>
                <TabPanel value="0">
                  <RadioGroup
                    row
                    aria-labelledby="row-radio-buttons-group-label"
                    name="row-radio-buttons-group"
                    value={number}
                    onChange={handleNumberChange}
                    sx={{
                      marginBottom: 2
                    }}
                  >
                    <Stack spacing={1}>
                      <Box>
                        <Typography component={'p'} fontSize="small">
                          {' 2 '}
                        </Typography>
                        <FormControlLabel value="1" control={<Radio />} label="" />
                        <FormControlLabel value="2" control={<Radio />} label="" />
                        <FormControlLabel value="3" control={<Radio />} label="" />
                        <FormControlLabel value="4" control={<Radio />} label="" />
                      </Box>
                      <Box>
                        <Typography component={'p'} fontSize="small">
                          {' 4 '}
                        </Typography>
                        <FormControlLabel value="6" control={<Radio />} label="" />
                        <FormControlLabel value="7" control={<Radio />} label="" />
                        <FormControlLabel value="8" control={<Radio />} label="" />
                        <FormControlLabel value="9" control={<Radio />} label="" />
                      </Box>
                      <Box>
                        <Typography component={'p'} fontSize="small">
                          {' 8 '}
                        </Typography>
                        <FormControlLabel value="0" control={<Radio />} label="" />
                        <FormControlLabel value="5" control={<Radio />} label="" />
                      </Box>
                    </Stack>
                  </RadioGroup>
                  <TextField
                    type="number"
                    value={ amount }
                    onChange={ (event: React.ChangeEvent<HTMLInputElement>) => { setAmount(event.target.value); } }
                    InputProps={{
                      endAdornment: <InputAdornment position="end">USDT</InputAdornment>,
                    }}
                    label=""
                    helperText=" 100-10000 USDT"
                    size="small"
                    fullWidth
                    sx={{
                      marginBottom: 2
                    }}
                  />
                  <Button
                    variant="contained"
                    size="large"
                    onClick={purchaseAction}
                  >
                    {buttonText}
                  </Button>
                  {
                    hasEarned &&
                    <Button
                      variant="outlined"
                      size="large"
                      onClick={claimAction}
                      sx={{
                        marginLeft: 1
                      }}
                    >
                      {''}
                    </Button>
                  }
                </TabPanel>
                <TabPanel value="1"></TabPanel>
              </TabContext>
              <Collapse
                in={ alert }
                sx={{
                  position: 'absolute',
                  zIndex: 999,
                  bottom: 0,
                }}
              >
                <Alert
                  severity={ alertSeverity }
                  onClose={ () => setAlert(false) }
                  sx={{
                    marginY: 2
                  }}
                >
                  { alertMessage }
                </Alert>
              </Collapse>
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Layout>
  );
};

export default Bet;
